
  import { defineComponent, ref, watch } from "vue"

  const props = {
    show: {
      type: Boolean,
      default: false
    }
  }
  const emits = ["onClose"]
  const components = {}
  export default defineComponent({
    name: "ModalDialog",
    props,
    emits,
    components,
    setup(props) {
      const showModal = ref(false)

      watch(
        () => props.show,
        (show) => {
          showModal.value = show
        }
      )

      return {
        showModal
      }
    }
  })
